.DandelionIcon {
  background-color: rgb(18, 18, 112);
  border: none;
  position: absolute;
  height: 70px;
  width: 70px;
  border-radius: 50%;
  background-image: url(./img/dandelionIcon.png);
  background-size: cover;
  background-repeat: no-repeat;
  bottom: 50px;
  right: 50px;
  transition: 0.5s;
  animation: reminder2 6s ease infinite;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba(158, 111, 119, 0.637);
    border-radius: 50%;
    opacity: 0.5;
  }
  &:hover {
    transform: scale(1.2);
    background-color: rgb(52, 52, 136);
  }
}

.text {
  padding: 5px 35px 5px 5px;
  text-align: center;
  position: absolute;
  bottom: 70px;
  right: 130px;
  width: 180px;
  height: auto;
  color: black;
  font-size: 1.32rem;
  background-color: #fffed4;
  -webkit-clip-path: polygon(82% 0, 82% 58%, 100% 81%, 80% 100%, 0 100%, 0 0);
  clip-path: polygon(82% 0, 82% 58%, 100% 81%, 80% 100%, 0 100%, 0 0);
  animation: reminder 6s ease infinite;

  p {
    color: #000;
  }
}

.Info {
  background-color: #fff;
  color: black;
  position: absolute;
  top: 0;
  right: -400px;
  width: 400px;
  height: 100vh;
  z-index: 4;
  transition: 1s;
  overflow: -moz-scrollbars-none;
  overflow-y: scroll;

  &::-webkit-scrollbar {
    width: 0 !important;
  }

  img {
    width: 100%;
    height: auto;
  }

  h3 {
    font-size: 2rem;
    margin-top: 20px;
    margin-left: 10px;
  }

  p {
    padding: 20px;
    font-size: 1.5rem;
  }
}

.active {
  @extend .Info;
  right: 0;
}

.DandelionIconClose {
  border: 1px solid black;
  height: 30px;
  width: 30px;
  line-height: 30px;
  font-size: 3rem;
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 50%;
  background-color: #fff;
  color: #000;
  transform: rotate(45deg);
  transition: 0.5s;

  &:hover {
    transform: rotate(135deg);
    background-color: #999;
  }
}

@keyframes reminder {
  0% {
    transform: translateX(0) scale(1);
  }
  20% {
    transform: translateX(-10px) scale(1.1);
  }
  40% {
    transform: translateX(0) scale(1);
  }
}

@keyframes reminder2 {
  0% {
    background-color: rgb(18, 18, 112);
  }
  20% {
    background-color: rgb(52, 52, 136);
  }
  40% {
    background-color: rgb(18, 18, 112);
  }
}
